<template>
  <div>

    <div class="total">
      <div class="fondo text-center">
        <img src="/favicon.ico" width="100" alt="logo" class="mb-4">
        <h1 class="titulo">Puestito Online</h1>
        <form class="row" @submit.prevent="login">
          <div class="width-size">
            <label for="usuario">Usuario</label>
            <input class="form-control mb-3" type="text" v-model="usuario" required />
          </div>
          <div class="width-size">
            <label for="contrasea">Contrasea</label>
            <input class="form-control mb-4" type="password" v-model="contrasea" required />
          </div>
          <div class="col-md-8">
            <button type="submit" class="btn btn-entrar">Entrar</button>
          </div>
          <router-link class="mt-4" to="/u/planes">Registrar Negocio?</router-link>
          <router-link class="mt-4" to="/">Volver a Puestito Online</router-link>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  components: {
  },
  data() {
    return {
      usuario: "",
      contrasea: "",
    };
  },
  methods: {
    checkAuthentication() {
      const isAuthenticated = !!localStorage.getItem("token");/* Agrega aqu tu lgica para verificar si el usuario est autenticado */
      if (isAuthenticated) {
        this.$router.push("/u/home");
      }
    },
    async login() {
      try {
        // Realiza una solicitud HTTP para iniciar sesin en el backend
        const response = await axios.post("/login", {
          usuario: this.usuario,
          contrasea: this.contrasea,
        });

        // Maneja la respuesta del backend
        if (response.status === 200) {

          const fecha = await axios.post('/comprobar-vencimiento', { usuario: response.data.nomUsuario });
          const fechaVence = fecha.data;
          // Almacena el token en el almacenamiento local
          localStorage.setItem("token", response.data.token);
          localStorage.setItem("nombre", response.data.nombre);
          localStorage.setItem("usuario", response.data.nomUsuario);

          localStorage.setItem("ao", fechaVence);
          // Redirige al usuario a la pgina de inicio (por ejemplo, /u/home)
          this.$router.push("/u/home");
          const Toast = Swal.mixin({
            toast: true,
            position: 'bottom-end',
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener('mouseenter', Swal.stopTimer)
              toast.addEventListener('mouseleave', Swal.resumeTimer)
            }
          })

          Toast.fire({
            icon: 'success',
            title: 'Inicio de sesin exitoso.'
          })
        }
      } catch (error) {
        Swal.fire({
          icon: 'error',
          text: 'Usuario/contrasea incorrectos.'
        });
      }
    }
  },
  mounted() {
    this.checkAuthentication();
  },
};
</script>

<style scoped>
a {
  text-decoration: none;
}

.btn-entrar {
  color: white;
  width: 50%;
  height: 60px;
  background: linear-gradient(rgb(175, 210, 255), rgb(0, 90, 207)) !important;
  font-size: large;
  border: none;
  box-shadow: 0.5px 1px 4px black;
  border-radius: 1px;
}

.btn-entrar:hover {
  background: linear-gradient(rgb(175, 210, 255), rgb(0, 87, 168)) !important;
}

h1.titulo {
  display: block;
  color: black;
}

label {
  color: black;
}

.total {
  height: calc(100svh - 56px);
  background-color: #8cfbff;
}

.fondo {
  display: block;
  justify-content: center;
  padding-top: 10vh;
}

form {
  justify-content: center;
  width: 40vw;
  margin: auto;
}

.titulo {
  display: block;
}

.width-size {
  width: 30vw;
}

@media screen and (max-width: 992px) {
  form{
    width: auto;
  }
  .width-size {
    width: 80vw !important;
  }

  .total {
    height: calc(100svh - 56px);
  }

  .fondo {
    width: 100vw;
  }
}
</style>